.header {
  border-bottom: 1px solid $border;
  flex: 0 0 auto;

  &--no-js {
    & .header__btn-nav {
      width: 1px;
      height: 1px;
      margin: -1px;
      border: 0;
      padding: 0;
      white-space: nowrap;
      clip: rect(0 0 0 0);
      overflow: hidden;
    }

    & .header__nav {
      display: block;
    }
  }

  @media (max-width: $tablet-width) {
    border: none;
  }
}

.header__wrapper {
  display: grid;
  grid-template-columns: 1fr 66px 1fr;
  max-width: 1230px;
  min-height: 66px;
  padding: 30px 15px;
  padding-bottom: 14px;

  &--active {
    background-color: $brown;

    & > .header__btn-nav::after {
      background-color: $white;
      box-shadow: 0 8px 0 0 $white, 0 -8px 0 0 $white;
    }
  }

  @media (max-width: $tablet-width) {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: $mobile-width) {
    padding: 20px 0 0 0;
  }
}

.header__container {
  width: 235px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  justify-self: flex-end;

  @media (max-width: $tablet-width) {
    width: 100%;
    grid-row: 1;
    grid-column: 3/4;
    align-items: center;
  }
}

.header__login,
.header__cart {
  padding: 13px 18px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: $black;
  display: block;
  height: min-content;

  &:hover,
  &:focus {
    color: $brown;
  }

  &:active {
    color: rgba($brown, 0.7);
  }

  @media (max-width: $mobile-width) {
    padding: 13px;
  }
}

.header__login {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header__cart-text {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header__cart {
  @media (max-width: $tablet-width) {
    margin-right: 26px;
    margin-top: -1px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 1px;
    margin-top: -1px;
  }
}

.header__cart-num {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header__search {
  margin-top: 4px;
  position: relative;
  padding-left: 15px;

  &::after {
    content: "";
    position: absolute;
    top: 11px;
    width: 14px;
    height: 14px;
    background-image: url("../img/icon/search.svg");
    background-repeat: no-repeat;
    display: block;
  }

  & input {
    margin-left: 27px;
    border: none;
    width: 192px;
    height: 35px;

    &:hover,
    &:focus {
      border-bottom: 1px solid $black;
    }

    &::placeholder {
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: $placeholder;
    }
  }

  @media (max-width: $tablet-width) {
    display: none;
  }

  &--nav {
    display: block;
    grid-column: 1/-1;
    position: relative;
    margin-top: 40px;
    padding: 0 45px;

    &::after {
      content: "";
      position: absolute;
      top: 12px;
      left: 57px;
      width: 16px;
      height: 16px;
      background-image: url("../img/icon/search-white.svg");
      background-repeat: no-repeat;
    }

    @media (max-width: $mobile-width) {
      margin-top: 30px;
      padding: 0 15px;

      &::after {
        left: 27px;
      }
    }

    & input {
      margin-left: 0;
      width: 100%;
      border: none;
      border-radius: 21px;
      background-color: rgba($white, 0.15);
      height: 40px;
      padding-left: 40px;
      color: $white;

      &:hover,
      &:focus {
        border-bottom: none;
      }

      &::placeholder {
        color: rgba($white, 0.7);
      }
    }
  }
}

.header__logo {
  grid-column: 2/3;
  grid-row: 1/2;

  @media (max-width: $mobile-width) {
    & svg {
      width: 52px;
      height: 52px;
      margin: 0 auto;
    }
  }
}

.header__cart-svg {
  display: none;
  fill: transparent;

  @media (max-width: $tablet-width) {
    display: block;
    margin-right: 0;

    stroke: $black;

    &--white {
      stroke: $white;
    }
  }

  @media (max-width: $mobile-width) {
    margin-right: 0;
  }
}

.header__btn-nav {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    width: 50px;
    margin: 8px 32px;
    border: none;
    background-color: transparent;
    height: 50px;

    &::after {
      content: "";
      display: block;
      margin: 0 auto;
      width: 25px;
      height: 2px;
      background-color: $black;
      box-shadow: 0 8px 0 0 $black, 0 -8px 0 0 $black;
    }
  }

  @media (max-width: $mobile-width) {
    margin: 1px 2px;
  }
}

.header__nav {
  display: none;
  grid-column: 1/-1;
  background-color: $brown;
  padding-bottom: 20px;

  &--active {
    display: block;
    position: fixed;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    overflow-y: scroll;
  }

  @media (max-width: $mobile-width) {
    &--active {
      top: 142px;
    }
  }
}

.header__logo-svg {
  fill: $brown;

  @media (max-width: $tablet-width) {
    display: block;

    &--white {
      fill: $white;
    }
  }
}

.header__nav-items {
  display: flex;
  flex-direction: column;
  margin: 22px 45px;
  margin-bottom: 18px;

  @media (max-width: $mobile-width) {
    margin: 28px 15px 12px;
  }
}

.header__nav-item {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: $white;
  padding: 10px 0;

  &:hover,
  &:focus {
    color: $light-gray;
  }

  &:active {
    color: rgba($light-gray, 0.7);
  }
}

.header__nav-info {
  display: flex;
  flex-direction: column;
  margin: 24px 45px;
  margin-bottom: 36px;

  & a {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: rgba($white, 0.75);
    padding: 10px 0;

    &:hover,
    &:focus {
      color: $white;
    }

    &:active {
      color: rgba($white, 0.7);
    }
  }

  @media (max-width: $mobile-width) {
    margin: 12px 15px 25px;
  }
}

.header__nav-login {
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: $white;
  margin: 45px;

  &:hover,
  &:focus {
    color: $light-gray;
  }

  &:active {
    color: rgba($light-gray, 0.7);
  }

  @media (max-width: $mobile-width) {
    margin: 16px;
  }
}
