.shop {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 33px;
  padding: 0 50px;

  &--no-js {
    & .shop__wrapper {
      flex-wrap: wrap;
      justify-content: space-between;
    }

    & .shop__slide {
      width: 290px;

      @media (max-width: $tablet-width) {
        width: 324px;

        &:nth-child(2n + 1) {
          margin-right: 15px;
        }
      }

      @media (max-width: $mobile-width) {
        width: 130px;
      }
    }
  }

  @media (max-width: $mobile-width) {
    padding: 0 15px;
  }
}

.shop__slide {
  transition: 0.5s;

  &:hover,
  &:focus {
    transform: scale(1.05);
  }
}

.shop__head {
  font-family: "CormorantGaramond", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 130%;
  color: $black;
  text-align: center;

  @media (max-width: $tablet-width) {
    margin-bottom: 20px;
  }
}

.shop__btn {
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: $black;
  border-bottom: 1px solid $brown;
  display: block;
  width: max-content;
  margin: 0 auto;
  margin-bottom: 57px;

  &:hover,
  &:focus {
    color: $brown;
  }

  &:active {
    color: rgba($brown, 0.7);
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 54px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 43px;
  }
}

.shop__prod-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
  color: $black;
  margin-top: 21px;
  margin-bottom: 8px;

  @media (max-width: $mobile-width) {
    margin-top: 12px;
  }
}

.shop__prod-price {
  font-weight: bold;
  font-size: 14px;
  line-height: 130%;
  text-align: center;
  color: $black;
  margin-top: 0;
}

.shop__img {
  max-width: 100%;
  height: auto;

  & img {
    width: 100%;
    height: 100%;
  }

  @media (max-width: $tablet-width) {
    max-width: 100%;
    height: auto;
    max-height: 100%;
  }
}

.shop__container {
  width: 100%;
  max-width: 1170px;
  margin-top: 57px;

  @media (max-width: $tablet-width) {
    max-width: 100%;
    height: auto;
    max-height: 100%;
    margin-top: 9px;
  }

  @media (max-width: $mobile-width) {
    max-width: 100%;
    height: auto;
    max-height: 100%;
    margin-top: 0;
  }

  & .swiper-wrapper {
    margin-bottom: 60px;

    @media (max-width: $tablet-width) {
      margin-bottom: 48px;
    }

    @media (max-width: $mobile-width) {
      margin-bottom: 34px;
    }
  }

  & .swiper-pagination {
    bottom: 0;
  }

  & .swiper-pagination-bullet {
    font-family: "Poppins", "Courier New", monospace;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: $placeholder;
    opacity: 1;
    width: 13px;
    height: min-content;
    background-color: transparent;

    &:hover,
    &:focus {
      color: $black;
    }

    &:active {
      color: rgba($black, 0.7);
    }


    &-active {
      color: $black;
    }
  }

  & .swiper-pagination-fraction {
    font-family: "Poppins", "Courier New", monospace;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: $black;
  }
}
